package com.github.salomonbrys.revealkt

import kotlin.browser.document

external object hljs {
    fun initHighlightingOnLoad()
}

object RevealPlugins {
    fun highlightJs(revealRoot: String) = revealDependency {
        src = "$revealRoot/plugin/highlight/highlight.js"
        async = true
        callback = { hljs.initHighlightingOnLoad() }
    }

    fun marked(revealRoot: String) = revealDependency {
        src = "$revealRoot/plugin/markdown/marked.js"
        condition = { document.querySelector( "[data-markdown]") != null; }
    }

    fun markdown(revealRoot: String) = revealDependency {
        src = "$revealRoot/plugin/markdown/markdown.js"
        condition = { document.querySelector( "[data-markdown]") != null; }
    }

    fun zoom(revealRoot: String) = revealDependency {
        src = "$revealRoot/plugin/zoom-js/zoom.js"
        async = true
    }

    fun speakerNotes(revealRoot: String) = revealDependency {
        src = "$revealRoot/plugin/notes/notes.js"
        async = true
    }

    fun mathJax(revealRoot: String) = revealDependency {
        src = "$revealRoot/plugin/math/math.js"
        async = true
    }

    fun multiplex(revealRoot: String) = revealDependency {
        src = "$revealRoot/plugin/multiplex/master.js"
        async = true
    }

    fun serverSpeakerNotes(revealRoot: String) = revealDependency {
        src = "$revealRoot/plugin/notes-server/client.js"
        async = true
    }

    fun printPDF(revealRoot: String) = revealDependency {
        src = "$revealRoot/plugin/print-pdf/print-pdf.js"
    }

    fun search(revealRoot: String) = revealDependency {
        src = "$revealRoot/plugin/search/search.js"
        async = true
    }

    fun socketsIoCdn() = revealDependency {
        src = "//cdn.socket.io/socket.io-1.3.5.js"
        async = true
    }
}
