package com.github.salomonbrys.revealkt

import kotlinx.html.*
import kotlinx.html.consumers.onFinalize
import kotlinx.html.js.div
import kotlinx.html.stream.appendHTML
import org.w3c.dom.HTMLElement
import org.w3c.dom.HTMLImageElement
import org.w3c.dom.set

fun TagConsumer<HTMLElement>.revealSlides(block : DIV.() -> Unit = {}) {
    div(classes = "reveal") {
        div(classes = "slides", block = block)
    }
}

fun TagConsumer<HTMLElement>.slide(classes: String? = null, id: String? = null, transition: String? = null, transitionSpeed: String? = null, state: String? = null, block: SECTION.() -> Unit = {}) = section(classes) {
    id?.let { this.id = id }
    transition?.let { attributes["data-transition"] = it }
    state?.let { attributes["data-state"] = it }
    transitionSpeed?.let { attributes["data-transition-speed"] = it }
    block()
}

fun SECTION.backgroundColor(color: String) {
    attributes["data-background-color"] = color
}

fun SECTION.backgroundImage(src: String, size: String? = null, position: String? = null, repeat: String? = null) {
    attributes["data-background-image"] = src
    size?.let { attributes["data-background-size"] = it }
    position?.let { attributes["data-background-position"] = it }
    repeat?.let { attributes["data-background-repeat"] = it }
}

fun SECTION.backgroundVideo(src: String, loop: Boolean = false, muted: Boolean = false, size: String? = null) {
    attributes["data-background-video"] = src
    if (loop) attributes["data-background-position"] = ""
    if (muted) attributes["data-background-repeat"] = ""
    size?.let { attributes["data-background-size"] = it }
}

fun SECTION.backgroundIframe(href: String, interactive: Boolean = false) {
    attributes["data-background-iframe"] = href
    if (interactive) attributes["data-background-interactive"] = ""
}

fun SECTION.backgroundTransition(transition: String) {
    attributes["data-background-transition"] = transition
}

fun SECTION.notes(block: ASIDE.() -> Unit) = aside("notes", block)

class AnimText {
    private class Span(val classes: String, val update: SPAN.() -> Unit)

    private val spans = ArrayList<Span>()

    fun span(classes: String, text: String, update: SPAN.() -> Unit = {}): String {
        val count = spans.size
        spans.add(Span(classes, update))
        return "@@$count[$text]$count@@"
    }

    fun fragment(index: Int, classes: String, text: String, update: SPAN.() -> Unit = {}) =
            span("fragment $classes", text) { attributes["data-fragment-index"] = index.toString() ; update() }

    fun applyTo(orig: String): String {
        if (spans.isEmpty()) return orig

        var txt = orig

        spans.forEachIndexed { index, fragment ->
            val match = Regex("@@$index\\[([\\s\\S]*)]$index@@", RegexOption.MULTILINE).find(txt) ?: throw IllegalStateException("Could not find span $index markers")

            val span = StringBuilder().appendHTML(prettyPrint = false).span(fragment.classes) {
                fragment.update(this)
                unsafe { +match.groupValues[1] }
            }.toString()

            txt = txt.substring(0, match.range.start) + span + txt.substring(match.range.endInclusive + 1)
        }

        return txt
    }
}

fun AnimText.hl(index: Int, text: String) = fragment(index, "highlight-current-blue", text)

fun AnimText.fIn(index: Int, text: String) = fragment(index, "", text)

fun AnimText.fOut(index: Int, text: String) = fragment(index, "fade-out", text)

fun AnimText.cur(index: Int, text: String) = fragment(index, "current-visible", text)

fun CODE.animText(txt: AnimText.() -> String): String = asDynamic().animText.unsafeCast<AnimText>().txt()

fun TagConsumer<HTMLElement>.hlCode(lang: String? = null, block: CODE.() -> Unit = {}) {
    pre {
        val animText = AnimText()

        onFinalize { element, partial ->
            if (partial) return@onFinalize

            element.innerHTML = animText.applyTo(element.innerHTML)
        }
        .code(classes = lang) {
            asDynamic().animText = animText

            attributes["data-trim"] = ""
            attributes["data-noescape"] = ""
            block()
        }
    }
}

fun TagConsumer<HTMLElement>.fragment(classes: String? = null, index: Int = -1) = onFinalize { element, partial ->
    if (partial)
        return@onFinalize

    element.classList.add("fragment")
    if (classes != null)
        classes.split(Regex(" +")).forEach { element.classList.add(it) }
    if (index >= 0)
        element.dataset["fragmentIndex"] = index.toString()
}

fun TagConsumer<HTMLElement>.fadeIn(index: Int = -1) = fragment(null, index)
fun TagConsumer<HTMLElement>.grow(index: Int = -1) = fragment("grow", index)
fun TagConsumer<HTMLElement>.shrink(index: Int = -1) = fragment("shrink", index)
fun TagConsumer<HTMLElement>.fadeOut(index: Int = -1) = fragment("fade-out", index)
fun TagConsumer<HTMLElement>.fadeUp(index: Int = -1) = fragment("fade-up", index)
fun TagConsumer<HTMLElement>.curVisible(index: Int = -1) = fragment("current-visible", index)
fun TagConsumer<HTMLElement>.highlightCurBlue(index: Int = -1) = fragment("highlight-current-blue", index)
fun TagConsumer<HTMLElement>.highlightRed(index: Int = -1) = fragment("highlight-red", index)
fun TagConsumer<HTMLElement>.highlightGreen(index: Int = -1) = fragment("highlight-green", index)
fun TagConsumer<HTMLElement>.highlightBlue(index: Int = -1) = fragment("highlight-blue", index)


fun <T> TagConsumer<T>.lazyImg(alt : String? = null, src : String? = null, classes : String? = null, block : IMG.() -> Unit = {}) = img(alt, null, classes) {
    if (src != null)
        attributes["data-src"] = src
    block()
}
