package com.github.salomonbrys.revealkt

import org.w3c.dom.Element

private fun <T> createConfig(conf: T.() -> Unit): T {
    val obj = js("{}")
    (obj.unsafeCast<T>()).conf()
    return obj
}

fun revealDependency(conf: RevealDependency.() -> Unit) = createConfig(conf)

fun multiplexConfig(conf: MultiplexConfig.() -> Unit) = createConfig(conf)

fun mathConfig(conf: RevealDependency.() -> Unit) = createConfig(conf)

object Reveal {

    fun initialize(conf: RevealOptions.() -> Unit) = RevealJs.initialize(createConfig(conf))

    fun configure(diff: RevealOptions.() -> Unit) = RevealJs.configure(createConfig(diff))
    fun sync() = RevealJs.sync()

    fun slide(indexh: Int, indexv: Int? = null, indexf: Number? = null) = RevealJs.slide(indexh, indexv, indexf)

    fun left() = RevealJs.left()
    fun right() = RevealJs.right()
    fun up() = RevealJs.up()
    fun down() = RevealJs.down()
    fun prev() = RevealJs.prev()
    fun next() = RevealJs.next()

    fun navigateFragment(index: Int? = null, offset: Int) = RevealJs.navigateFragment(index, offset)
    fun prevFragment() = RevealJs.prevFragment()
    fun nextFragment() = RevealJs.nextFragment()

    fun layout() = RevealJs.layout()

    fun shuffle() = RevealJs.shuffle()

    fun availableRoutes() = RevealJs.availableRoutes()

    fun availableFragments() = RevealJs.availableFragments()

    fun toggleHelp(override: Boolean? = null) = RevealJs.toggleHelp(override)

    fun toggleOverview(override: Boolean? = null) = RevealJs.toggleOverview(override)
    fun togglePause(override: Boolean? = null) = RevealJs.togglePause(override)
    fun toggleAutoSlide(override: Boolean? = null) = RevealJs.toggleAutoSlide(override)

    fun isOverview() = RevealJs.isOverview()
    fun isPaused() = RevealJs.isPaused()
    fun isAutoSliding() = RevealJs.isAutoSliding()
    fun isSpeakerNotes() = RevealJs.isSpeakerNotes()

    fun setState(state: Any) = RevealJs.setState(state)
    fun getState() = RevealJs.getState()

    fun getSlidePastCount() = RevealJs.getSlidePastCount()

    fun getProgress() = RevealJs.getProgress()

    fun getIndices(slide: Element? = null) = RevealJs.getIndices(slide)

    fun getSlides(): Array<Element> = RevealJs.getSlides()
    fun getTotalSlides() = RevealJs.getTotalSlides()
    fun getSlide(x: Int, y: Int? = null) = RevealJs.getSlide(x, y)

    fun getSlideNotes(slide: Element? = null) = RevealJs.getSlideNotes(slide)

    fun getPreviousSlide() = RevealJs.getPreviousSlide()
    fun getCurrentSlide() = RevealJs.getCurrentSlide()

    fun getScale() = RevealJs.getScale()

    fun getConfig() = RevealJs.getConfig()

    fun getQueryHash() = RevealJs.getQueryHash()

    fun isFirstSlide() = RevealJs.isFirstSlide()
    fun isLastSlide() = RevealJs.isLastSlide()

    fun isReady() = RevealJs.isReady()

    fun addEventListener(type: String, useCapture: Boolean? = null, listener: (event: Any) -> Unit): (event: Any) -> Unit {
        RevealJs.addEventListener(type, listener, useCapture)
        return listener
    }

    fun removeEventListener(type: String, useCapture: Boolean? = null, listener: (event: Any) -> Unit) = RevealJs.removeEventListener(type, listener, useCapture)

    fun triggerKey(keyCode: Int) = RevealJs.triggerKey(keyCode)

    fun registerKeyboardShortcut(key: String, value: String) = RevealJs.registerKeyboardShortcut(key, value)

    fun addOnReady(f: (SlideEvent) -> Unit) = addEventListener("ready") { f(it.unsafeCast<SlideEvent>()) }

    fun addOnSlideChanged(f: (SlideChangedEvent) -> Unit) = addEventListener("slidechanged") { f(it.unsafeCast<SlideChangedEvent>()) }

    fun addOnFragmentShown(f: (FragmentEvent) -> Unit) = addEventListener("fragmentshown") { f(it.unsafeCast<FragmentEvent>()) }

    fun addOnFragmentHidden(f: (FragmentEvent) -> Unit) = addEventListener("fragmenthidden") { f(it.unsafeCast<FragmentEvent>()) }

    fun addOnOverviewShown(f: (SlideEvent) -> Unit) = addEventListener("overviewshown") { f(it.unsafeCast<SlideEvent>()) }

    fun addOnOverviewHidden(f: (SlideEvent) -> Unit) = addEventListener("overviewhidden") { f(it.unsafeCast<SlideEvent>()) }

    fun addOnPdfReady(f: () -> Unit) = addEventListener("pdf-ready") { f() }

    fun addOnPaused(f: () -> Unit) = addEventListener("paused") { f() }

    fun addOnResumed(f: () -> Unit) = addEventListener("resumed") { f() }

    fun addOnAutoSlidePaused(f: () -> Unit) = addEventListener("autoslidepaused") { f() }

    fun addOnAutoSlideResumed(f: () -> Unit) = addEventListener("autoslideresumed") { f() }

}
